import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getDataKondisiBarang } from "../../../actions/datamaster_action";
import { ReanderField } from "../../../components/helpers/field";
import { ToastNotification } from "../../../components/helpers/notification";
import Tabel from "../../../components/helpers/tabel";
import { postData } from "../../../config/axios";
import ValidasiBaralJual from "../../../Validasi/Penjualan/ValidasiBaralJual";
import { setBatalPembelian } from "../../../actions/penjualan_action";

const maptostate = (state) => {
  return {
    setBatalPembelian: state.penjualan.setBatalPembelian,
    datakondisi: state.datamaster.getDataKondisiBarang,
  };
};
class HeadBatalPembelian extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isloadingFaktur: false,
      isLoading: false,
      isLoadingBatal: false,
      date: new Date(),
    };
  }

  getFaktur(value) {
    this.setState({ no_faktur: value });
  }
  getDetailFaktur() {
    this.setState({ isLoading: true });
    let data = {
      no_faktur_group: this.state.no_faktur,
    };
    postData("batalpembelian/get/by-faktur", data)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Transaksi Tersbut Tidak Ada");
          this.setState({ isLoading: false });
        } else {
          this.props.dispatch(setBatalPembelian(res.data));
          this.setState({
            isLoading: false,
          });
        }
      })
      .catch((err) => {
        ToastNotification("info", "Data Transaksi Tersbut Tidak Ada");
        this.setState({ isLoading: false });
      });
  }
  componentDidMount() {
    this.props.dispatch(getDataKondisiBarang());
  }
  btlPembelian() {
    this.setState({ isLoadingBatal: true })
    setTimeout(() => {
      this.setState({ isLoadingBatal: false })
      this.props.dispatch(setBatalPembelian([]));
    }, 500);
  }
  render() {
    // const expandRow = {
    //   renderer: (row) => (
    //     <div>
    //       Nama Atribut : {row.nama_atribut} <br />
    //       Harga Atribut : {parseInt(row.harga_atribut).toLocaleString(
    //         "kr-KO"
    //       )}{" "}
    //       <br />
    //       Ongkos : {parseInt(row.ongkos).toLocaleString("kr-KO")} <br />
    //     </div>
    //   ),
    // };
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-lg-6">
            <Field
              onChange={(e) => this.getFaktur(e.target.value)}
              name="no_faktur"
              component={ReanderField}
              type="text"
              label="No Faktur"
              placeholder="Masukan No Faktur"
            />
          </div>

          <div className="col-lg-6">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="button"
                onClick={() => this.getDetailFaktur()}
                name="type_btn"
                className="btn btn-primary btn-block"
                disabled={this.state.isLoading}
              >
                {this.state.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Data"
                )}
              </button>
            </div>
          </div>
          <div className="col-lg-12">
            <Tabel
              keyField="kode_barcode"
              exportCsv={false}
              data={this.props.setBatalPembelian || []}
              columns={this.props.columns}
              empty={true}
              textEmpty="Data Barang Kosong"
              // expandRow={expandRow}
            />
          </div>
          <div className="col-lg-12">
            <label>&nbsp;</label>
            <button
              type="submit"
              name="type_btn"
              className="btn btn-primary"
              disabled={this.props.isLoading}
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Mengirim Data
                </>
              ) : (
                "Simpan Batal Pembelian"
              )}
            </button>
            &nbsp;
            <button
              type="button"
              name="type_btn"
              onClick={() => this.btlPembelian()}
              className="btn btn-warning"
              disabled={this.state.isLoadingBatal}
            >
              {this.state.isLoadingBatal ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Batal
                </>
              ) : (
                "Batal"
              )}
            </button>
          </div>
        </div>
      </form>
    );
  }
}

HeadBatalPembelian = reduxForm({
  form: "HeadBatalPembelian",
  enableReinitialize: true,
  validate: ValidasiBaralJual,
})(HeadBatalPembelian);
export default connect(maptostate, null)(HeadBatalPembelian);
