import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reset } from "redux-form";
import { setBatalPembelian } from "../../../actions/penjualan_action.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import { postData } from "../../../config/axios.jsx";
import HeadBatalPembelian from "./HeadBatalPembelian.jsx";

const maptostate = (state) => {
  return {
    setBatalPembelian: state.penjualan.setBatalPembelian,
  };
};
class FormBatalPembelian extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      databarang: [],
      columns: [
        {
          dataField: "no_faktur_beli",
          text: "No Faktur Beli",
          footerAttrs: {
            colSpan: "1",
          },
          footer: "Total Barang",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
          footer: (columnData) =>
          parseInt(this.props.setBatalPembelian.length)
        },

        {
          dataField: "berat_nota",
          text: "Berat Nota",
          formatter: (cell) => {
            return <span>{parseFloat(cell).toFixed(1)}</span>;
          },
          footer: (columnData) =>
            parseFloat(columnData.reduce((acc, item) => acc + item, 0)).toFixed(
              1
            ),
        },

        {
          dataField: "harga_nota",
          text: "Harga Nota",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },

        {
          dataField: "berat",
          text: "Berat",
          formatter: (cell) => {
            return <span>{parseFloat(cell).toFixed(1)}</span>;
          },
          footer: (columnData) =>
            parseFloat(columnData.reduce((acc, item) => acc + item, 0)).toFixed(
              1
            ),
        },
        {
          dataField: "harga",
          text: "Harga Emas",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    this.setState({ isLoading: true });
    let hasil = {
      no_faktur_group: data.no_faktur,
    };
    postData("batalpembelian/simpan", hasil)
      .then((res) => {
        this.setState({ isLoading: false });
        this.props.dispatch(setBatalPembelian([]));
        this.props.dispatch(reset("HeadBatalPembelian"));
        ToastNotification("success", res.data.message);
      })
      .catch((err) => {
        this.setState({ isLoading: false });
        ToastNotification("success", err.message);
      });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Pembelian</Link>
          </li>
          <li className="breadcrumb-item active">Batal Pembelian</li>
        </ol>
        <h1 className="page-header">Batal Pembelian </h1>
        <Panel>
          <PanelHeader>Batal Pembelian</PanelHeader>
          <br />
          <div className="container">
            <HeadBatalPembelian
              isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
              data={this.state.databarang}
              columns={this.state.columns}
            />
          </div>
          <br />
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(FormBatalPembelian);
